@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400;500;600;700&display=swap);

html{
    --bg: #F6F6FB;
    --section: rgba(255, 255, 255, 1);
    --text1: rgba(22, 22, 22, 0.87);
    --text2: #8F8F8F;
    --textInvert: rgba(233, 233, 233);
    --accent: #3763FE;
    --accentLight: #E7E9FB;
    --border: rgba(0,0,0,.1);
    --br: 20px;
   
}


    
html.dark {
    --text1: rgb(233, 233, 233);
    --text2: #919191;
    --textInvert: rgb(0, 0, 0);
    /* --bg: #000; */
    --bg: rgb(24, 23, 29);

    /* --section: #1c1d1fb6; */
    --section: rgb(35, 34, 43);

    --accent: #6989fc;
    --accentLight: #35363b;
    --border: rgba(255,255,255,.15  )
    }

html{background:var(--bg) !important}@media(max-width: 1000px){html{font-size:.9rem}}body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.base-container{max-width:1416px;margin:0 auto}.Container-1200{max-width:1200px;margin:0 auto}.tooltip{font-size:12px !important;padding:5px !important}.action-icon{color:var(--text2);width:23px;height:23px}.action-icon path{stroke-width:.8;stroke:var(--bg)}.action-icon:hover{color:var(--accent)}@media(max-width: 900px){.action-icon svg{width:20px;height:20px}}.page-header{position:relative;display:-webkit-flex;display:flex;padding-top:20px;padding-bottom:30px;-webkit-align-items:center;align-items:center;color:var(--text1);font-size:16px;font-weight:bold}.page-header h2{color:var(--text1)}.page-header span{color:var(--accent);margin-bottom:-5px !important;padding-right:1em}.page-header .search-box{position:absolute;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.page-header .search-input{color:var(--text1);border-radius:20px;border:1px solid var(--border);background:var(--bg);box-shadow:none !important}.page-header .search-input:active,.page-header .search-input:focus{border-color:var(--accent)}.page-header .search-input::before,.page-header .search-input::after{display:none !important}.action-block{margin-top:10px;display:-webkit-flex;display:flex;position:absolute;top:0;right:10px}.action-block div{padding:10px}.priority-table{width:100%;border:1px solid var(--border);border-radius:var(--br)}.priority-table thead th{padding:10px;color:var(--text2);font-size:11px;font-weight:400;transition:color .1s ease;border-bottom:1px solid var(--border);text-align:left;opacity:.5}.priority-table thead th:hover{opacity:1;color:var(--text1)}.priority-table tbody td{padding:10px;border-bottom:1px solid var(--border);color:var(--text2);font-size:12px}.priority-table tbody td:nth-of-type(2) a,.priority-table tbody td:nth-of-type(3){font-family:"IBM Plex Mono";text-decoration:none;color:var(--text1);font-size:15px}.priority-table tbody td:nth-of-type(4){color:var(--text1);font-size:14px}.priority-table tbody tr:last-of-type td{border-bottom:none}
.loginCard{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.loginCard input{background:inherit !important}.loginCard label{color:var(--text1) !important;font-weight:300;font-size:12px}.loginCard .Button{background-color:var(--text1) !important;color:var(--textInvert) !important}
.preorders-container #grow{height:0;overflow:hidden !important;transition:height 200ms ease-out;margin-bottom:20px}.preorders-container.active #grow{overflow:visible}.preorders-container.active svg{-webkit-transform:rotate(90deg);transform:rotate(90deg)}.preorders-heading{color:var(--accent) !important;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.preorders-heading span{margin:0 5px}.preorders-heading span svg{opacity:.8;width:21px;height:21px;margin-bottom:-6px;padding-bottom:0;transition-duration:.3s;-webkit-transform:rotate(0deg);transform:rotate(0deg)}.acceptOrderButton{width:30px;height:30px;background:var(--accentLight);border:2px solid transparent;border-radius:7px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin:0 auto}.acceptOrderButton:hover{border:2px solid var(--accent)}.acceptOrderButton svg{width:20px}.acceptOrderButton svg:hover{color:var(--accent)}
.group-heading{font-size:15px;font-weight:600;color:var(--text1);padding-bottom:7px}.priority-table{margin-bottom:50px}
.base-container section{background:var(--section);padding:0;border-radius:var(--br);min-height:100vh;color:var(--text1)}.OrderLayout{display:grid;grid-template-columns:2.5fr 1fr}.OrderLayout .Main .Composition{padding:3rem;line-height:2;border-bottom:1px solid var(--border)}.OrderLayout .Main .Composition .Unit{display:grid;grid-template-columns:4fr repeat(3, 1fr)}.OrderLayout .Main .Composition .Name,.OrderLayout .Main .Composition .Quantity{font-family:"IBM Plex Mono";font-size:20px}.OrderLayout .Main .Composition .Quantity{color:var(--accent);font-weight:600}.OrderLayout .Main .WrapperTwoCol{display:grid;grid-template-columns:1fr 1fr;border-bottom:1px solid var(--border)}.OrderLayout .Main .WrapperTwoCol .CheckList{padding:2rem}.OrderLayout .Main .WrapperTwoCol .CheckList .CheckListUnit{position:relative;padding:.4rem 3rem;font-size:1rem;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.OrderLayout .Main .WrapperTwoCol .CheckList .CheckListUnit:hover{background:var(--accentLight);border-radius:5px}.OrderLayout .Main .WrapperTwoCol .CheckList .CheckListUnit::after{content:"";top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);left:10px;position:absolute;width:15px;height:15px;border-radius:100%;opacity:.6;border:1px solid var(--accent)}.OrderLayout .Main .WrapperTwoCol .CheckList .CheckListUnit.complited{text-decoration:line-through;color:var(--text2)}.OrderLayout .Main .WrapperTwoCol .CheckList .CheckListUnit.complited::after{background:var(--accent)}.OrderLayout .Main .WrapperTwoCol .Docs{border-left:1px solid var(--border)}
.OrderLayout .Meta{border-left:1px solid var(--border);padding:2rem}.OrderLayout .Meta pre{font-size:11px;font-weight:400;color:var(--text1);margin:0;opacity:.4;transition:opacity 300ms ease}.OrderLayout .Meta .SignificantInfo{display:grid;grid-template-columns:1fr 1fr}.OrderLayout .Meta .SignificantInfo pre{opacity:1}.OrderLayout .Meta .SignificantInfo div{font-size:1.2rem;font-weight:700}.OrderLayout .Meta .SignificantInfo .PaymentHistory{padding:0 4px}.OrderLayout .Meta .SignificantInfo .PaymentHistory svg{height:18px;color:var(--accent)}.OrderLayout .Meta .MetaWrapper{display:grid;grid-template-columns:1fr 1fr;padding:1rem 0}.OrderLayout .Meta .MetaWrapper .Right div,.OrderLayout .Meta .MetaWrapper .Left div{padding:.6rem 0}.OrderLayout .Meta .MetaWrapper .Right div:hover pre,.OrderLayout .Meta .MetaWrapper .Left div:hover pre{opacity:1}.OrderLayout .Meta .OrderComment{color:var(--text1);font-weight:300;padding:1rem 0}.OrderLayout .Meta .OrderComment pre{padding-bottom:.5rem}
.reportMeta{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;font-size:.8em;color:var(--text1);margin:20px 0}.legend{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;font-size:.8em;color:var(--text2);line-height:1.8;padding-bottom:10px}.legend div{padding-left:40px;position:relative;margin:0 1rem}.legend .status-101::before,.legend .status-102::before{position:absolute;top:0;left:0;content:"";width:30px;height:20px}.legend .status-101::before{background:#c60bff3b}.legend .status-102::before{background:#15ff0034}.reportConfigurator{display:-webkit-flex;display:flex;background:var(--section);border-radius:15px;margin-bottom:1rem;font-size:.9em}.reportConfigurator .chooseMonth{padding:15px;display:-webkit-flex;display:flex}.reportConfigurator .chooseMonth div{padding:7px 10px;border-radius:7px;color:var(--text2);width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;transition:color .2s ease}.reportConfigurator .chooseMonth div:hover{color:var(--accent)}.reportConfigurator .chooseMonth .active{background:var(--accentLight);color:var(--accent)}.reportConfigurator .arguments{display:grid;grid-auto-flow:column;font-size:.9em}.reportConfigurator .arguments .wrap{border-left:1px solid var(--border);border-radius:0;padding:15px 1rem}.reportConfigurator .arguments .wrap span{color:var(--text2)}.reportConfigurator .arguments .wrap input{margin:0 7px;box-sizing:border-box;background:var(--section);border:none;font-weight:600;color:var(--accent);height:100%;width:25px;outline:none !important}.attendance-table{border:1px solid var(--border);padding:10px;border-radius:var(--br) !important;font-size:.8em;color:var(--text2);border-collapse:collapse;overflow-x:auto !important;width:100%}.attendance-table thead th{position:-webkit-sticky;position:sticky;top:-3px;background:var(--bg);border-bottom:1px solid var(--border);padding:13px 0;color:var(--text1);font-weight:normal}.attendance-table tbody td{border:1px solid var(--border) !important;padding:0}.attendance-table tbody td div{padding:2px}.attendance-table tbody td .status-101{background:#c60bff3b}.attendance-table tbody td:nth-of-type(1){color:var(--text1);padding-left:6px}.attendance-table tbody td:nth-of-type(2){color:var(--text1);font-weight:700;padding:0 7px}.attendance-table tbody td .eventTime{opacity:1;transition:opacity .1s ease}.attendance-table tbody td .interval{color:var(--text1)}
.Sidebar{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:10px 0;position:fixed;top:0;left:0;height:100vh;width:50px}.Sidebar .action-icon{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;box-shadow:inset 10px 0px 0px transparent;-webkit-align-self:center;align-self:center;padding:15px 5px;width:100%}.Sidebar .action-icon:nth-last-of-type(3){margin-top:auto}.Sidebar .sidebar-active{box-shadow:inset 8px 0px 0px var(--accent) !important;color:var(--accent) !important}
